import React from "react"
import { StaticQuery, graphql, Link } from "gatsby"


const menuQuery = graphql`{
  allWordpressWpApiMenusMenusItems {
      edges {
        node {
          name
          items {
            title
            url
            object_slug
           object
          }
        }
      }
    },
    site {
      siteMetadata {
        siteURL
      }
    }
    allWordpressWpMedia {
      edges {
        node {
          slug
          source_url
        }
      }
    }
  }  
`;


  let checkType = (props, key) =>{

    //Si entro a categoria le quito
    const propsVar = props.site.siteMetadata.siteURL;
    let link ='';
    const str = key.url;
    console.log('1'+str);

    if(key.object === 'category'){
      link = str.replace('https://', "");
      console.log('2'+link);
      link = link.replace(propsVar, "/");
      console.log('3'+link);
    }else{
      link = key.object_slug;
      console.log('4'+link);
    }
    return link
  }

  const menuNumber = (props) =>{
    return props.allWordpressWpApiMenusMenusItems.edges[1].node.items;
  }



const mainmenu =  () => (
  <StaticQuery
    query={menuQuery}
    render={props => (
      <header props={props} >

      <div className="collapsible-menu navbar  bg-faded col-12">
      <input type="checkbox" id="menu"  />
            <label htmlFor="menu">Menu</label>
            <div className="menu-content">
                <ul>
                  {
                  menuNumber(props).map(key =>(
                      <li className=' nav-link' key={key.title}>
                      <Link to={checkType(props, key)} key={key.title}>  {key.title}</Link>
                      </li>
                  ))
                  }
                </ul>
            </div>
      </div>

      // </header>
    )}
  />
)

export default mainmenu


